import React from 'react'
import { Link } from "gatsby"
import styled from "styled-components"

import posed from 'react-pose';

const List = posed.div({
  visible: { y: 0, opacity: 1, staggerChildren: 0, transition: { duration: 0}, applyAtStart: { display: 'block' }},
  hidden: { y: 0, opacity: 1, transition: { duration: 0}, applyAtEnd: { display: 'none' }}
});

const ListElem = posed.div({
  visible: { x: 0, opacity: 1, transition: { duration: 0}, display: 'block'},
  hidden: { x: 50, opacity: 0, transition: { duration: 0}, applyAtStart: { display: 'block' }}
});


const StyledThing = styled.li`

  background-color: white;
  list-style: none;
  margin: 0;
  padding: 5px 15px;
  white-space: nowrap;
  border-radius: 100px;
  font-family: 'Calibre-Regular';
  font-size: 24px;

  @media(min-width: 992px) {
    background-color: white;
    list-style: none;
    margin: 0;
    padding: 10px 15px;
    white-space: nowrap;
    border-radius: 100px;
    font-family: 'Calibre-Regular';
    font-size: 34px;
  }

`
const Capsule = styled.div`
  margin-left: 15px;
`
const StyledList = styled(List)`
    font-family: 'Calibre-Regular';
  font-size: 24px;
  position: relative;
  left: 0;
  width: 0; 
  white-space: nowrap; 
  background-color: white;
  color: black;
  min-height: 350px;

  @media(min-width: 992px) {
    font-family: 'Calibre-Regular';
    font-size: 34px;
    position: relative;
    left: 0;
    width: 0; 
    white-space: nowrap; 
    background-color: white;
    color: black;
    min-height: 350px;
  }
`

const StyledLink = styled(Link)`
position: relative;
text-decoration: none;
color: blue;
top: 4px;


@media(min-width: 992px) {
  top: 5px;
}
`

const StyledListElem = styled(ListElem)`
  margin: 15px 0 0 0;

  @media(min-width: 992px) {
    margin: 20px 0 0 0;
  }
`

const ListEl = styled.span`
  background-color: white;
  padding: 8px 10px 0px 10px
`

const ListElBis = styled.span`
  background-color: white;
  padding: 8px 10px 0px 10px;
  border-radius: 100px
`

class StyledLiEven extends React.Component {
    constructor(props){
        super(props);

        this.state = {
            isVisible: false
        }

        this.mouseEnter = this.mouseEnter.bind(this);
        this.mouseLeave = this.mouseLeave.bind(this);
    }

    componentDidMount(){
        if(this.props.animated === false) {
          this.setState({
            isVisible: true
          });
        }
      }

    mouseEnter(){

        this.setState({
          isVisible: true
        });
      }
    
      mouseLeave(){
        this.setState({
          isVisible: false
        });

        if(this.props.animated === false) {
          this.setState({
            isVisible: true
          });
        }
      }

    render(){
        const {isVisible} = this.state;
                let counter = 0;
          

        return(
            <>
            <Capsule>
            <StyledThing onMouseEnter={this.mouseEnter} onMouseLeave={this.mouseLeave}>
                <StyledLink to={this.props.item ? `/${this.props.item.node.uid}/` : ''}>{this.props.item.node.data.title.text}</StyledLink>
            </StyledThing>
        <StyledList pose={this.state.isVisible ? 'visible' : 'hidden'}>

          {this.props.item.node.tags.map((item, index) => {
             counter ++;
            if (counter % 2 === 0) {
              return (
                <StyledListElem key={index}>
                <ListElBis>
                  {item}
                </ListElBis>
              </StyledListElem>
              )
            } else {
              return (
                <StyledListElem key={index}>
                <ListEl>
                  {item}
                </ListEl>
              </StyledListElem>
              )
            }
          })
        }
          </StyledList>
          </Capsule>
        </>
        )
    }
}

export default StyledLiEven;
