import { Link } from "gatsby"
import React from "react"
import styled from "styled-components"
import { StaticQuery, graphql } from "gatsby"
import PerfectScrollbar from 'react-perfect-scrollbar'

import {TweenLite, Power3} from "gsap/TweenMax";
 
//or get to the parts that aren't included inside TweenMax:
import Draggable from "gsap/Draggable";
import ScrollToPlugin from "gsap/ScrollToPlugin";



import StyledLiOdd from './styledliodd'
import StyledLiEven from './styledlieven'


const MenuTopStyled = styled.div`
  -webkit-overflow-scrolling: touch;
  position: fixed; 
  width: 100vw; 
  z-index: 999;
  display: flex;
  flex-direction: row;
  top: 25px;
`

const StyledPerfectScrollbar = styled(PerfectScrollbar)`
  overflow-x: scroll;
  position: fixed;
  display: flex;
  width: 100vw;
  top: 15px
`

class MenuTop extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      menuItems: null
    };
    
  
    this.addElements = this.addElements.bind(this);
    this.handleScroll = this.handleScroll.bind(this);

  }

  componentWillMount(){

    const data = this.props.data.allPrismicPage.edges;

  //   const menuItemsArray = [
  //   {name:'Ziegert Report 2018/19', slug:'ziegertreport', tags:['Annual Report', 'Editorial Design', 'Content Marketing', 'Social Media', 'PR']},
  //   {name:'Ziegert - One for all', slug:'ziegertreport', tags:['Campaign', 'Film', 'Banner', 'Landingpage', 'Social Media']},
  //   {name:'Gemeindehaus', tags:['Corporate Identity', 'Naming', 'Interior Design', 'Campaign', 'Events']},
  //   {name:'Becken', tags:['Corporate Design', 'Logo', 'Storytelling', 'Brand Architecture', 'Webdesign']},
  //   {name:'EverEstate', tags:['Brand Identity', 'Naming', 'Storytelling', 'Editorial Design', 'Sales']},
  //   {name:'Zoom', tags:['Investment Memorandum', 'Naming', 'Editorial Design', 'Storytelling', 'Production']},
  //   {name:'BNKR', tags:['Cultural Identity', 'Naming', 'Design', 'Brand Experience', 'Advertising']},
  //   {name:'Sternhöhe Möhringen', tags:['Brand Identity', 'Naming', 'Design', 'Storytelling', 'Sales']}
  // ];

  console.log(data);

  // const menuItemsArray = [
  //   {name:'Ziegert Report 2018/19', slug:'ziegertreport', tags:['Annual Report', 'Editorial Design', 'Content Marketing', 'Social Media', 'PR']},
  //   {name:'Ziegert - One for all', slug:'ziegertreport', tags:['Campaign', 'Film', 'Banner', 'Landingpage', 'Social Media']},
  //   {name:'Gemeindehaus - Creative Workspace', tags:['Corporate Identity', 'Naming', 'Interior Design', 'Campaign', 'Events']},
  //   {name:'Becken - 1978', tags:['Corporate Design', 'Logo', 'Storytelling', 'Brand Architecture', 'Webdesign']},
  //   {name:'EverEstate - Wiliam Bay', tags:['Brand Identity', 'Naming', 'Storytelling', 'Editorial Design', 'Sales']},
  //   {name:'Lührmann - Zoom', tags:['Investment Memorandum', 'Naming', 'Editorial Design', 'Storytelling', 'Production']},
  //   {name:'Euroboden Architekturkultur - BNKR', tags:['Cultural Identity', 'Naming', 'Design', 'Brand Experience', 'Advertising']},
  //   {name:' alstria REIT-Office - Sternhöhe Möhringen', tags:['Brand Identity', 'Naming', 'Design', 'Storytelling', 'Sales']}
  // ];
    
    // this.setState({
    //   menuItems: menuItemsArray
    // });

    this.setState({
        menuItems: data
      });

  }

  addElements() {

    let arrayToDuplicate = this.state.menuItems;
    
    this.setState(prevState => ({
        menuItems: [...prevState.menuItems, ...arrayToDuplicate]
    }));

  }

  handleScroll() {
      // console.log('scrolled');

      // const menuTop = this.menuRef.current;
      // const scrollBar = document.querySelector('.ps__rail-x');

      this.addElements();

    // if (menuTop.scrollLeft + menuTop.clientWidth >= menuTop.scrollWidth) {

    //     console.log('scrolled to end!');

    //     this.addElements();

    //   }

  }

  componentDidMount(){

    this.addElements();

        
    Draggable.create(this.menuRef, {type:"scrollLeft"});
    
    setTimeout(function(){
      TweenLite.to(this.menuRef, 2, {scrollTo:{x:250}, ease:Power3.easeInOut});
    }.bind(this),1000);


  }
  


  render() {
    const {menuItems} = this.state;

    let counter = 0;


    return (
            <StyledPerfectScrollbar className="menu-top" onXReachEnd={this.handleScroll} containerRef={ div => this.menuRef = div }>
            {/* <MenuTopStyled className="menu-top" onScroll={this.handleScroll} ref={this.menuRef}> */}
            {menuItems.map((item, index) => {
      
              counter ++;
      
              if (counter % 2 === 0 ) {
                return (
                  <StyledLiEven
                  key={index} 
                  item={item}
                  />
                )
              } else {
                return (
                  <StyledLiOdd 
                  key={index} 
                  item={item}/>
                )
              }
            }
            )}
            {/* </MenuTopStyled> */}
            </StyledPerfectScrollbar>
    )
  }
}


// export default MenuTop

export default props => (
    <StaticQuery
      query={graphql`
        query {
            allPrismicPage {
                edges {
                  node {
                    tags
                    uid
                    data {
                      title {
                        text
                      }
                    }
                  }
                }
              }
        }
      `}
      render={data => <MenuTop data={data} {...props} />}
    />
  )
